<template>
  <div class="hello">
      <p class="page-title">当前位置:账户管理</p>
        <div class="search">
            <Input v-model="userName" placeholder="用户名" style="width: 150px" />
            <Button type="primary">搜索</Button>
            <Button type="primary" @click="addUser">添加账户</Button>
        </div>
         <Table border :columns="columns" :data="data6"></Table>
       <Modal
        v-model="modal1"
       width="340"
        title="添加账户"
        @on-ok="ok"
       >
         <Input v-model="value" placeholder="用户名" style="width: 300px;margin-bottom:10px;" />
         <Input v-model="value1"type="password" placeholder="密码" style="width: 300px;margin-bottom:10px;" />
        <Select v-model="model1" clearable   style="width: 300px;margin-bottom:10px;"  placeholder="选择角色" >
                <Option v-for="item in cityList" :value="item.id":key="item.id">{{ item.title }}</Option>
            </Select>
    </Modal>
   
  </div>
</template>
<script>
var that = null;
 export default {
        data () {
            return {
                modal1:false,
                userName:"",
                value:"",
                model1:"",
                value1:"",
                page:1,
                cityList:[],
                columns: [
                    {
                        title: '用户名',
                        key: 'userName',
                       
                    },
                    {
                        title: '密码',
                        key: 'password'
                    },
                    {
                        title: '状态',
                        key: 'status'
                    },
                     {
                        title: '角色',
                        key: 'role_title'
                    },
                     {
                        title: '创建',
                        key: 'create_time'
                    },
                    {
                        title: 'Action',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('span', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px',
                                        cursor:"pointer"
                                    },
                                    hover:{
                                        color:"#e66"
                                    },
                                    on: {
                                        click: () => {
                                            // this.show(params.index)
                                        }
                                    }
                                },  params.row.status==2?'开启':'关闭'),
                               
                            ]);
                        }
                    }
                ],
                data6: [ ]
            }
        },
        mounted(){
            that = this;
            this.pageinfo();
        },
        methods: {
            ok(){
                    var data ={
                        userName:this.value,
                        password:this.value1,
                        role:this.model1,
                    }

                    this.cityList.forEach(val=>{
                            if(val.id==this.model1){
                                data.role_title = val.title
                            }
                    })

                   var res =  this.$testdata(data);
                    if(res){
                    this.$api.adduser({
                                data:data,
                                success:function(res){
                                   that.pageinfo();
                                }
                        })
                    }

                   



            },
            pageinfo(){
                this.$api.getuser({
                    data:{current_page:this.page},
                    success:res=>{
                        console.log(res.data)
                            that.data6 = res.data.list
                    }
                })

                  this.$api.getuserlist({
                    data:{
                        type:2 , 
                        current_page:this.page},
                    success:res=>{
                            that.cityList = res.data.list;

                    }
                })
            },
            addUser(){
                    this.modal1 = true


            },
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name：${this.data6[index].name}<br>Age：${this.data6[index].age}<br>Address：${this.data6[index].address}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            }
        }
    }
</script>



